<template>
  <div class="home">
    <top-part @navigate="navigateTo" />
    <strategy />
    <reviews />
    <cases @contact="openContactDialog" />
    <explanation @contact="openContactDialog" />
    <faq @contact="openContactDialog" />
  </div>
</template>

<script>
import TopPart from "@/components/Home/TopPart.vue";
import Strategy from "@/components/Home/Strategy.vue";
// import Info from "@/components/Home/Info.vue";
import Reviews from "@/components/Home/Reviews.vue";
import Cases from "@/components/Home/Cases.vue";
import Explanation from "@/components/Home/Explanation.vue";
import Faq from "@/components/Home/FAQ.vue";

import { useCommonStore } from "@/store/common.store";

export default {
  components: {
    TopPart,
    Strategy,
    // Info,
    Reviews,
    Cases,
    Explanation,
    Faq,
  },
  setup() {
    const commonStore = useCommonStore();

    function navigateTo(name) {
      document
        .querySelector(`.${name}`)
        .scrollIntoView({ block: "start", behavior: "smooth" });
    }

    function openContactDialog(type, data) {
      commonStore.initNewDialog({
        type,
        data,
      });
    }

    return {
      navigateTo,
      openContactDialog,
    };
  },
};
</script>